'use client';

import React from 'react';
import Link from 'next/link';

import Header from '@/../components/header'

export default function AuthLayout({ children }: { children: React.ReactNode; }) {
    
    return (
        <>
        
            <Header/>

            <main className=" py-6 px-4 bg-ccDarkGray">
                {children}
            </main>
            
        </>
    );
}